@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body{
    font-family: 'Roboto', sans-serif;
}
.logo{
    width: 100px;
    height: 100px;
    color: white;
}

.intro{
    display: flex;
}
.welcome-area{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 50%;
    height: 100vh;
    background: linear-gradient(#764DF4 30%, #4D57F4);
    color: white;
}
.welcome-area i{
    font-size: 100px;
}
.intro-heading{
    font-size: 40px;
    text-align: center;
}
.main-form{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 50%;
    height: 100vh;
}
.name-label{
    margin-right: 10px;
}
#name{
    padding: 10px;
}
textarea{
    margin-top: 50px;
}
.submit{
    padding: 13px 20px;
    background-color: #4D57F4;
    color: white;
    border: none;
    margin-top: 15px;
    cursor: pointer;
    transition: 0.3s;
}
.submit:active{
    transform: scale(.9);
}